
/* eslint-disable no-restricted-globals */
self.onmessage = async (event) => {
  const { matrixData } = event.data; // Receive processed data
  try {
    // Perform any data processing here if needed
    self.postMessage(matrixData); // Send the processed data back
  } catch (error) {
    console.error("Worker Error:", error);
    self.postMessage({ error: "Failed to process data." });
  }
};

